import ApprovedModal from '../../UgcRights/Status/ApprovedModal'
import Pendingng from '../../UgcRights/Status/PendingModal'
import RejectModal from '../../UgcRights/Status/RejectModal'

const UgcStatus = (props) => {
    const { ugcData, statusPopup, ugcRequestData, onHide, openugcModel } = props
    return (<>
        {statusPopup == 1 ? <Pendingng ugcData={ugcData} ugcRequestData={ugcRequestData} onHide={onHide} /> : null}
        {statusPopup == 3 ? <RejectModal ugcRequestData={ugcRequestData} ugcData={ugcData} onHide={onHide}  openugcModel={openugcModel} /> : null}
        {statusPopup == 2 ? <ApprovedModal ugcRequestData={ugcRequestData} onHide={onHide} /> : null}
    </>)
}
export default UgcStatus